<template>
  <div class="forms-header">
    <div class="forms-header__file-name-container">
      <FormName :title="name" />

      <VStatus :status="status" />
    </div>

    <FormTypeSwitch v-model="type" />
  </div>
</template>

<script>
import FormName from "@/components/forms/FormName";
import VStatus from "@/components/forms/FormsStatus";
import FormTypeSwitch from "@/components/forms/FormTypeSwitch";
import { mapMutations } from "vuex";

export default {
  name: "FormsHeader",
  components: { FormTypeSwitch, VStatus, FormName },
  props: {
    formId: [Number, String],
  },
  methods: {
    ...mapMutations("formInfo", ["changeFormType"]),
  },
  computed: {
    name() {
      return this.$store.state.formInfo.name;
    },
    status() {
      return this.$store.state.formInfo.status;
    },
    type: {
      get() {
        return this.$store.state.formInfo.type;
      },
      set(type) {
        this.changeFormType(type);
      },
    },
  },
};
</script>

<style scoped lang="scss">
.forms-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.forms-header__file-name-container {
  display: flex;
  align-items: center;
}
</style>
